
@mixin _mobile {
    @media (min-width: 350px) and (max-width: 539px) {
        @content;
    }
}
@mixin _540-767 {
    @media (min-width: 540px) and (max-width: 767px) {
        @content;
    }
}
@mixin _768-900 {
    @media (min-width: 768px) and (max-width: 900px) {
        @content;
    }
}